.content-container {
  padding: 2rem 16px;
  .content{
    display: flex;
    align-items: center;
    p{
        font-size: 16px;
        min-width: 200px;
    }
    .radio-group{
      margin: 5px 0;
      label{
          font-size: 12px;
          margin-right: 16px;
          cursor: pointer;
      }
      input{
          cursor: pointer;
      }
    }
  }
}
.operations {
  border-top: 1px solid black;
}
button {
  background-color: black;
  color: white;
  padding: 4px 8px;
  border-radius: 2px;
  min-width: 80px;
  margin-right: 8px;
  &:disabled{
    background-color: rgb(138, 138, 138);
  }
}
.btn {
  display: flex;
  justify-content: space-between;
  margin: 16px 0;
}